:root {
  --main-color: #f6f2ff;
  --main-deepColor: #cec3e7;
  --primary: #328b94;
  --apple: #f5f5f7;
  /* dark mode */
  --dark: #191919;
  --dark-container: #1c1c1c;
  --dark-hover: #2a2929;
  --dark-border: #2e2e2e;
  --dark-text: #fff;
  /* light mode */
  --light: #fff;
  --light-container: var(--apple);
  --light-hover: var(--apple);
  --light-border: #cecece;
  --light-text: #000;
  /* stat card colors */
  --stat-card-one: linear-gradient(135deg, rgba(255, 255, 196, 1.000) 0.000%, rgba(255, 97, 100, 1.000) 50.000%, rgba(176, 0, 18, 1.000) 100.000%);
  --stat-card-two: linear-gradient(45deg, rgba(250, 218, 97, 1.000) 0.000%, rgba(255, 145, 136, 1.000) 50.000%, rgba(255, 90, 205, 1.000) 100.000%);
  --stat-card-three: linear-gradient(45deg, rgba(142, 197, 252, 1.000) 0.000%, rgba(141, 211, 255, 1.000) 25.000%, rgba(161, 216, 255, 1.000) 50.000%, rgba(193, 210, 255, 1.000) 75.000%, rgba(224, 195, 255, 1.000) 100.000%);
