  }
  .dark\\:hover\\:bg-red-300:hover {
    --tw-bg-opacity: 1;
    background-color: rgb(252, 165, 165, var(--tw-bg-opacity));
  }
  .dark\\:active\\:bg-red-200:active {
    --tw-bg-opacity: 1;
    background-color: rgb(254, 202, 202, var(--tw-bg-opacity));
  }
}
